<template>
	<div class="inputs_box" v-title data-title="投入品监管">
		<ul class="total_card">
			<li class="item_card" v-for="item in cardColumn" v-show="item.show">
				<ul class="card_list">
					<li class="bar_card">
						<div :class="[classFn(item.theme), 'title']">{{ item.bar.title }}</div>
						<div class="echartbox" :ref="item.bar.ref"></div>
					</li>

					<li class="card" v-for="subitem in item.list" v-cloak>
						<input-card
							:cardtit="subitem.title"
							:tabletype="subitem.tableType"
							:interface="subitem.interfaceType"
							:themeid="item.theme"
							:modaldate.sync="subitem.modaldate"
							@selectListen="selectFn"
						>
							<el-table :data="subitem.table" header-row-class-name="headerline" row-class-name="rowline" stripe :ref="subitem.tableref" height="100%" width="100%">
								<el-table-column type="index" :index="indexMethod" :label="subitem.tablelabel[0]" min-width="20%"></el-table-column>
								<el-table-column :prop="subitem.tablekey[1]" :label="subitem.tablelabel[1]" :formatter="cellFilter" min-width="55%"></el-table-column>

								<el-table-column :prop="subitem.tablekey[2]" :label="subitem.tablelabel[2]" min-width="25%"></el-table-column>
							</el-table>
						</input-card>
					</li>
				</ul>
			</li>
		</ul>
	</div>
</template>

<script>
import inputCard from '../components/inputs/card';
import {_initBScroll, maxDecimalPlaces, areaLevelCorAreaId, prevMonth, dateFormat} from '../common/fn';
import {numTofixed} from '@/assets/js/common2';

const farmAreaInfo = areaLevelCorAreaId();

export default {
	data() {
		return {
			barEcharts: [], // 存放柱状图
			cardColumn: [
				{
					show: false,
					theme: 1,
					bar: {
						title: '种苗用量',
						ref: 'bar1',
						xData: [],
						yData: [],
					},
					list: [
						{
							modaldate: '',
							tableType: 1, // 统计数据分类，1代表种苗，2代表肥料，4代表农药
							interfaceType: 0, // 接口，0代表来源接口，1代表种类接口
							title: '种苗来源', // 卡片标题
							tableref: 'table1',
							tablelabel: ['排名', '厂商名称', '用量（吨）'], // table表头
							tablekey: ['productor', 'productor', 'totalAmount'], // table单元格字段名，要对应后台接口字段
							table: [], // table数据
						},
						{
							modaldate: '',
							tableType: 1,
							interfaceType: 1,
							title: '种苗种类',
							tableref: 'table2',
							tablelabel: ['排名', '种苗种类', '用量（吨）'],
							tablekey: ['resName', 'resName', 'totalAmount'],
							table: [],
						},
					],
				},
				{
					show: true,
					theme: 2,
					bar: {
						title: '肥料用量',
						ref: 'bar2',
						xData: [],
						yData: [],
					},
					list: [
						{
							modaldate: '',
							tableType: 2,
							interfaceType: 0,
							title: '肥料来源',
							tableref: 'table3',
							tablelabel: ['排名', '厂商名称', '用量（吨）'],
							tablekey: ['productor', 'productor', 'totalAmount'],
							table: [],
						},
						{
							modaldate: '',
							tableType: 2,
							interfaceType: 1,
							title: '肥料种类',
							tableref: 'table4',
							tablelabel: ['排名', '肥料种类', '用量（吨）'],
							tablekey: ['resName', 'resName', 'totalAmount'],
							table: [],
						},
					],
				},
				{
					show: true,
					theme: 3,
					bar: {
						title: '农药用量',
						ref: 'bar3',
						xData: [],
						yData: [],
					},
					list: [
						{
							modaldate: '',
							tableType: 4,
							interfaceType: 0,
							title: '农药来源',
							tableref: 'table5',
							tablelabel: ['排名', '厂商名称', '用量（吨）'],
							tablekey: ['productor', 'productor', 'totalAmount'],
							table: [],
						},
						{
							modaldate: '',
							tableType: 4,
							interfaceType: 1,
							title: '农药种类',
							tableref: 'table6',
							tablelabel: ['排名', '农药种类', '用量（吨）'],
							tablekey: ['resName', 'resName', 'totalAmount'],
							table: [],
						},
					],
				},
			],
			bscroll: [
				[null, null],
				[null, null],
				[null, null],
			],
		};
	},
	components: {inputCard},
	created() {},
	async mounted() {
		window.onresize = this.debounce(this.resizehandler, 300); // 函数抖动

		await this.getBarData(1);
		await this.getBarData(2);
		await this.getBarData(4);

		const now = new Date(new Date().setMonth(new Date().getMonth() - 1, 1)).format('yyyy-MM') + '-01';

		const This = this;
		this.cardColumn.forEach(function (item) {
			item.list.forEach(function (subitem, subindex) {
				This.getProSpec(subitem.tableType, subitem.interfaceType, subitem.modaldate);

				if (subitem.tableType == 1) {
					This.bscroll[0][subitem.interfaceType] = _initBScroll(This.$refs[This.cardColumn[0].list[subitem.interfaceType].tableref][0].$el.querySelector('.el-table__body-wrapper'));
				} else if (subitem.tableType == 2) {
					This.bscroll[1][subitem.interfaceType] = _initBScroll(This.$refs[This.cardColumn[1].list[subitem.interfaceType].tableref][0].$el.querySelector('.el-table__body-wrapper'));
				} else {
					This.bscroll[2][subitem.interfaceType] = _initBScroll(This.$refs[This.cardColumn[2].list[subitem.interfaceType].tableref][0].$el.querySelector('.el-table__body-wrapper'));
				}
			});
		});
	},
	methods: {
		debounce(method, delay) {
			let timer = null;
			return function () {
				const context = this;
				const args = arguments;
				clearTimeout(timer);
				timer = setTimeout(function () {
					method.apply(context, args);
				}, delay);
			};
		},
		resizehandler() {
			this.barEcharts.forEach(function (item, index) {
				item.resize();
			});
		},
		async getBarData(type) {
			// 获取柱状图数据
			const This = this;
			const months = this.$dateTimeUtil.monthLen();
			let bigMonth = null;
			let bigAmount = 0;
			return this.$ajax
				.post(this.$api.inputs.getBarData, {
					areaId: farmAreaInfo.farmAreaId,
					areaType: farmAreaInfo.farmAreaType,
					dateStrList: months,
					inputType: type,
				})
				.then(res => {
					if (res.data.respCode === 0) {
						if (type === 1) {
							This.cardColumn[0].bar.xData = [];
							This.cardColumn[0].bar.yData = [];
							res.data.obj.zmtVOS.forEach(function (item, index) {
								/* 冗余代码，可能会删除 */
								if (bigAmount <= item.totalAmount) {
									bigMonth = new Date(item.date).format('yyyy-MM-dd');
									bigAmount = item.totalAmount;
								}
								/* end */
								This.cardColumn[0].bar.xData.push(new Date(item.date).format('yyyy.MM'));
								This.cardColumn[0].bar.yData.push(item.totalAmount);
							});
							/* 冗余代码，可能会删除 */
							// console.log(dateFormat(new Date(prevMonth()).setDate(1), 'yyyy-MM-dd'));
							This.cardColumn[0].list[0].modaldate = dateFormat(new Date(prevMonth()).setDate(1), 'yyyy-MM-dd'); // bigMonth;
							This.cardColumn[0].list[1].modaldate = dateFormat(new Date(prevMonth()).setDate(1), 'yyyy-MM-dd'); // bigMonth;
							/* end */
							This.barInit(This.$refs[This.cardColumn[0].bar.ref][0], This.cardColumn[0].bar.xData, This.cardColumn[0].bar.yData);
						} else if (type === 2) {
							This.cardColumn[1].bar.xData = [];
							This.cardColumn[1].bar.yData = [];
							res.data.obj.fltVOS.forEach(function (item, index) {
								if (bigAmount <= item.totalAmount) {
									bigMonth = new Date(item.date).format('yyyy-MM-dd');
									bigAmount = item.totalAmount;
								}
								This.cardColumn[1].bar.xData.push(new Date(item.date).format('yyyy.MM'));
								This.cardColumn[1].bar.yData.push(item.totalAmount);
							});
							// console.log(dateFormat(new Date(prevMonth()).setDate(1), 'yyyy-MM-dd'));
							This.cardColumn[1].list[0].modaldate = dateFormat(new Date(prevMonth()).setDate(1), 'yyyy-MM-dd'); // bigMonth;
							This.cardColumn[1].list[1].modaldate = dateFormat(new Date(prevMonth()).setDate(1), 'yyyy-MM-dd'); // bigMonth;
							This.barInit(This.$refs[This.cardColumn[1].bar.ref][0], This.cardColumn[1].bar.xData, This.cardColumn[1].bar.yData);
						} else {
							This.cardColumn[2].bar.xData = [];
							This.cardColumn[2].bar.yData = [];
							res.data.obj.nytVOS.forEach(function (item, index) {
								if (bigAmount <= item.totalAmount) {
									bigMonth = new Date(item.date).format('yyyy-MM-dd');
									bigAmount = item.totalAmount;
								}
								This.cardColumn[2].bar.xData.push(new Date(item.date).format('yyyy.MM'));
								This.cardColumn[2].bar.yData.push(item.totalAmount);
							});
							// console.log(dateFormat(new Date(prevMonth()).setDate(1), 'yyyy-MM-dd'));
							This.cardColumn[2].list[0].modaldate = dateFormat(new Date(prevMonth()).setDate(1), 'yyyy-MM-dd'); // bigMonth;
							This.cardColumn[2].list[1].modaldate = dateFormat(new Date(prevMonth()).setDate(1), 'yyyy-MM-dd'); // bigMonth;
							This.barInit(This.$refs[This.cardColumn[2].bar.ref][0], This.cardColumn[2].bar.xData, This.cardColumn[2].bar.yData);
						}
						return true;
					}
				})
				.catch(error => {
					console.log('error:', error);
					return false;
				});
		},
		getProSpec(type, ajaxType, month) {
			// 获取来源数据和种类数据
			const This = this;
			const ajaxUrl = ajaxType ? this.$api.inputs.getSpecies : this.$api.inputs.getProductor;

			const index1 = type == 4 ? 2 : type - 1;
			if (This.bscroll[index1][ajaxType]) {
				This.bscroll[index1][ajaxType].scrollTo(0, 0);
			}

			this.$ajax
				.post(ajaxUrl, {
					areaId: farmAreaInfo.farmAreaId,
					areaType: farmAreaInfo.farmAreaType,
					dateStr: String(month).split('.').join('-'),
					inputType: type,
				})
				.then(res => {
					if (res.data.respCode == 0) {
						let tabs = null;
						// 去除未知后 排名前20
						if (type === 1) {
							tabs = ajaxType ? res.data.obj.zmResNameVOS.slice(0, 21) : res.data.obj.zmVOS.slice(0, 21);
							/* This.$nextTick(() => {
              This.bscroll[0][ajaxType].refresh();
            }); */
						} else if (type === 2) {
							// console.log(res.data.obj);
							tabs = ajaxType ? res.data.obj.flResNameVOS.slice(0, 21) : res.data.obj.flVOS.slice(0, 21);
							/* This.$nextTick(() => {
              This.bscroll[1][ajaxType].refresh();
            }); */
						} else {
							tabs = ajaxType ? res.data.obj.nyResNameVOS.slice(0, 21) : res.data.obj.nyVOS.slice(0, 21);
							/* This.$nextTick(() => {
              This.bscroll[2][ajaxType].refresh();
            }); */
						}
						// console.log(type);
						// console.log(tabs);
						tabs = tabs.filter(info => {
							return ajaxType ? info.resName !== '未知' : info.productor !== '未知';
						});
						if (tabs.length > 20) {
							tabs.pop();
						}
						if (type === 1 || type === 2) {
							This.cardColumn[type - 1].list[ajaxType].table = tabs;
							This.$nextTick(() => {
								This.bscroll[type - 1][ajaxType].refresh();
							});
						} else {
							This.cardColumn[2].list[ajaxType].table = tabs;
							This.$nextTick(() => {
								This.bscroll[2][ajaxType].refresh();
							});
						}
					}
				})
				.catch(error => {
					console.log('error:', error);
				});
		},
		selectFn(params) {
			if (params.refresh) {
				this.getProSpec(params.tabletype, params.interface, params.time + '.01');
			}
		},
		indexMethod(index) {
			return index + 1;
		},
		cellFilter(row, column, cellValue, index) {
			if (!cellValue) {
				return '未知';
			} else {
				return cellValue;
			}
		},
		barInit(ele, xData, yData) {
			const option = {
				grid: {
					top: '30',
					left: '13%',
					right: '5%',
					bottom: '22%',
				},
				tooltip: {
					trigger: 'item',
					formatter: function (params) {
						var substr = params.name + '<br />';
						if (params.data >= 100000000) {
							return substr + numTofixed(params.data / 100000000, 2) + '亿吨';
						} else {
							return params.data > 10000 ? substr + numTofixed(params.data / 10000, 2) + '万吨' : substr + numTofixed(params.data, 2) + '吨';
						}
					},
				},
				xAxis: {
					data: xData,
					axisLabel: {
						textStyle: {
							color: '#6DC1CB',
							fontSize: 12,
						},
						align: 'center',
					},
					axisTick: {
						show: false,
					},
					splitLine: {
						show: false,
					},
					axisLine: {
						show: true,
						lineStyle: {
							color: '#0D394A',
						},
					},
					z: 10,
				},
				yAxis: {
					type: 'value',
					name: '单位：吨',
					nameTextStyle: {
						fontSize: 14,
						color: '#6DC1CB',
					},
					splitNumber: 3,
					axisLine: {
						show: true,
						lineStyle: {
							color: '#0D394A',
						},
					},
					axisTick: {
						show: false,
					},
					splitLine: {
						lineStyle: {
							color: '#0D394A',
						},
					},
					axisLabel: {
						formatter: function (value, index) {
							if (value >= 100000000) {
								return numTofixed(value / 100000000, 2) + '亿';
							} else {
								return value >= 10000 ? numTofixed(value / 10000, 2) + '万' : numTofixed(value, 2);
							}
						},
						textStyle: {
							color: '#6DC1CB',
							fontSize: 12,
						},
					},
				},
				series: [
					{
						type: 'bar',
						barWidth: 24,
						data: yData,
						itemStyle: {
							// 通常情况下：
							normal: {
								// 每个柱子的颜色即为colorList数组里的每一项，如果柱子数目多于colorList的长度，则柱子颜色循环使用该数组
								color: function (params) {
									var colorList = ['#F0C87B', '#A6D889', '#DD90CB', '#9173FE', '#4DCBFF', '#1DE9B6', '#ca8622', '#bda29a', '#6e7074', '#546570', '#c4ccd3'];
									return colorList[params.dataIndex];
								},
							},
						},
					},
				],
			};

			const chart = this.$echarts.init(ele);
			chart.setOption(option);
			this.barEcharts.push(chart);
		},
		classFn(themeId) {
			if (themeId == 2) {
				return 'color2';
			} else if (themeId == 3) {
				return 'color3';
			} else {
				return 'color1';
			}
		},
		valueProcess(row, column, cellValue) {
			return maxDecimalPlaces({value: cellValue, places: 3});
		},
	},
};
</script>

<style scoped lang="scss">
@import '~styles/tableSkin_default';
@import '~styles/selectSkin_default';
@import '~styles/dialogSkin_default';
@import '~styles/datePickerSkin_default';
@import '~styles/scrollbarSkin_default';
</style>
<style scoped lang="less">
.card /deep/ .el-popper[x-placement^='bottom'] {
	z-index: 9999 !important;
}

/deep/ .el-table tr {
	height: 35px;

	td,
	th {
		padding: 0;

		& > .cell {
			white-space: nowrap;
			font-size: 14px;
			text-overflow: initial;
		}
	}
}

.inputs_box {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	min-height: 696px;

	.total_card {
		width: 100%;
		height: 100%;
		flex: none;
		.flex-center(space-around, flex-start);
		flex-flow: row nowrap;

		.item_card {
			width: 40%;
			height: 100%;
			padding: 20px 0;
			box-sizing: border-box;

			.card_list {
				height: 100%;
				padding: 45px 50px 10px;
				box-sizing: border-box;
				/*background: url("../assets/images/inputs/inputs_bg.png") no-repeat center;*/
				background: url('../assets/images/inputs/inputs_02_bg.png') no-repeat center;
				background-size: 100% 100%;

				.card {
					background: #061f36;
					padding: 12px 15px 15px;
					height: calc(100% / 3);
					overflow: hidden;
					box-sizing: border-box;
					margin-bottom: 15px;

					.card-content {
						height: 100%;

						/deep/ .el-table {
							height: calc(100% - 35px) !important;
						}

						/deep/ .el-table__body-wrapper {
							overflow: hidden;
							width: 100%;
						}
					}
				}

				.bar_card {
					padding-top: 10px;
					background: #061f36;
					height: 25%;
					margin-bottom: 15px;
					.flex-center(center, center);
					flex-flow: column;

					.title {
						height: 40px;
						font-size: 16px;
						color: #00f6ff;
						height: 32px;
						line-height: 32px;
						padding: 0 30px;
						background: url('../assets/images/inputs/input_tit_bg1.png') no-repeat center;
						background-size: 100% 100%;
					}

					.title.color2 {
						color: #9173fe;
						background: url('../assets/images/inputs/input_tit_bg2.png') no-repeat center;
					}

					.title.color3 {
						color: #1de9b6;
						background: url('../assets/images/inputs/input_tit_bg3.png') no-repeat center;
					}

					.echartbox {
						width: 100%;
						height: 200px;
					}
				}
			}
		}
	}
}

.flex-center(@x:center,@y:center) {
	display: flex;
	display: -webkit-flex;
	flex-flow: row nowrap;
	justify-content: @x;
	align-items: @y;
}
</style>



